html {
    box-sizing: border-box;
    font-size: 100%;
    padding: 0;
    margin: 0;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    padding: 1.25rem;
    background-color: $ltGray;
    color: $black;
    font-family: $font-family;
    line-height: 1.3;
}

main {
    //padding: 1.25rem;
    background: $white;
    border-radius: 1em;
    box-shadow: $shadow;
    margin: 0 auto;
    width: 80%;
}

h2,
h4 {
    font-weight: 700;
}

h2 {
    color: $Cyan;
}

h4 {
    color: $brightYellow;
}

.box-two,
.box-three {
    color: $white;
    padding: 2rem;

    h4 {
        color: $white;
    }
}

.box-one {
    padding: 2rem;
}

.box-two {
    background: $Cyan;

    p {
        font-weight: 300;

        span {
            font-size: 2em;
            font-weight: 700;
        }
    }
    a {
        text-decoration: none;
        padding: 1.25rem 4rem;
        background: $brightYellow;
        color: $white;
        display: block;
        text-align: center;
        box-shadow: $shadow;
        font-size: 1rem;

        @media only screen and (width: 280px) {
            padding: 1.25rem 0.5rem;
        }

          @media only screen and (min-width: 620px) {
            padding: 1.25rem 5rem;
        }
    }
}

.box-three {
    background: $garyBlue;
    border-radius: 0 0 1em 1em;
    ul {
        list-style: none;
        padding: 0;
        color: $ltGray;

        li {
            color: $ltGray;
        }
    }
}

//Responsiveness

@media only screen and (min-width: 620px) {
    .content {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-auto-rows: minmax(10rem, auto);
    }

    .box-one {
        padding: 3rem;
        grid-column: 1/3;
        grid-row: 1;
    }

    .box-two {
        border-radius: 0 0 0 1rem;
        padding: 3rem;
    }

    .box-three {
        border-radius: 0 0 1rem 0;
        padding: 3rem;
    }

    footer {
        text-align: center;
    }
}

@media only screen and (width: 280px) {
    html {
        font-size: 10px;
    }
}
